import * as React from 'react';
import { Product } from '../models/Product';
import Result from './Result';

type props = {
  products: Product[]
  resultType: string
  pageTitle: string
  filterValues: string[]
}

class state {
  activeFilter: string
  showFilter: boolean
}

class Results extends React.Component<props, state> {
  constructor(Props: props) {
    super(Props);
    this.state = {
      activeFilter: "All",
      showFilter: false
    };
  }

  onFilterClick(value: string) {
    $('.filter-' + this.state.activeFilter.replace("/", "-")).removeClass("active-filter"); // Must work with class names without using special characters (such as '/')
    $('.filter-' + value.replace("/", "-")).addClass("active-filter");
    this.state = {
      activeFilter: value,
      showFilter: this.state.showFilter
    }
    this.setState(this.state);
  }

  onShowHideFilter() {
    $('.show-hide-filter').text((this.state.showFilter == true) ? "Show filter" : "Hide filter"); // Toggle "show filter" text

    if (this.state.showFilter) $('.mobile-filter-categories').hide(); // Toggle filter buttons
    else $('.mobile-filter-categories').show();

    this.state = {
      activeFilter: this.state.activeFilter,
      showFilter: !this.state.showFilter
    }
    this.setState(this.state);
  }

  render() {
    return (<div>
      <br />
      <h2 className="text-center header">{this.props.pageTitle}</h2>
      <div className="container">
        <hr />
        <div className="mobile-filter text-center">
          <div className="show-hide-filter btn btn-outline-dark" onClick={() => this.onShowHideFilter()}>Show filter</div>
          <div className="mobile-filter-categories">
            <div className="row justify-content-center">
              {this.props.filterValues.map((value: string) => {
                return <div onClick={() => this.onFilterClick(value)} className={this.state.activeFilter == value ? "active-filter filter filter-" + value.replace("/", "-") : "filter filter-" + value.replace("/", "-")}>{value}</div>
              })}
            </div>
          </div>
        </div>
        <div className="desktop-filter">
          <div className="row justify-content-center">
            {this.props.filterValues.map((value: string) => {
              return <div onClick={() => this.onFilterClick(value)} className={this.state.activeFilter == value ? "active-filter filter filter-" + value.replace("/", "-") : "filter filter-" + value.replace("/", "-")}>{value}</div>
            })}
          </div>
        </div>
        <div className="row text-center text-lg-left">
          {this.props.products["products"].map((product: Product, id = 0) => {
            if (this.props.filterValues.indexOf(this.state.activeFilter) < 0){
              if (product.category == this.props.resultType) return <Result {...product} key={id} />
              else return null
            } else if (this.state.activeFilter != "All") {
              if (product.category == this.props.resultType && product.filter == this.state.activeFilter.toLowerCase().replace("/", "-")) return <Result {...product} key={id} />
              else return null
            } else {
              if (product.category == this.props.resultType) return <Result {...product} key={id} />
              else return null
            }
          })}
        </div>
      </div>
    </div>
    );
  }
}

export default Results;




