import * as React from 'react';
import { Switch, Route } from 'react-router-dom';
import Home from './Home';
import { Product } from '../models/Product';
import Results from './Results';

function windowsScrollToTop() {
  window.scrollTo(0, 0);
}

const Listings = (products: Product[]) => (

  <main>
    {windowsScrollToTop()}
    <Switch>
      <Route exact path="/" render={() => <Home />} />
      <Route path="/clothing" render={() => <Results products = {products} resultType = "clothing" pageTitle = "Clothing" filterValues = {["All", "Tops", "Bottoms", "Jumpers/Jackets", "Dresses/Jumpsuits"]}/>} />
      <Route path="/accessories" render={() => <Results products = {products} resultType = "accessories" pageTitle = "Accessories" filterValues = {["All", "Watches", "Sunnies", "Necklaces", "Earrings", "Rings"]}/>} />
      <Route path="/intimates" render={() => <Results products = {products} resultType = "intimates" pageTitle = "Intimates" filterValues = {[]}/>} />
    </Switch>
  </main>
);

export default Listings; 



