import * as React from 'react';
import { Product } from '../models/Product';
import ProductCarousel from './Product_Carousel';
import { Link } from 'react-router-dom';

let products: Product[] = require('../featured_products.json');

const Home = () => (
    <div>
        <header>
            <div className="container">
                <div className="intro-text text-center">
                    <h1 className="text-center">On-trend and replica designer fashion from Aliexpress</h1>
                    <Link className="btn btn-outline-dark see-more" to="/clothing">Shop Now</Link>
                </div>
            </div>
        </header>
        <div className="container">
            <br />
            <h2 className="text-center small-header">Clothing</h2>
            <br />
            <div className="justify-content-center">
                <ProductCarousel {...products['clothing']} />
                <div className="text-center">
                    <Link className="btn btn-outline-dark see-more" role="button" aria-pressed="true" to="/clothing">See More</Link>
                </div>
            </div>
            <h2 className="text-center small-header">Accessories</h2>
            <br />
            <div className="justify-content-center">
                <ProductCarousel {...products['accessories']} />
                <div className="text-center">
                    <Link className="btn btn-outline-dark see-more" to="/accessories">See More</Link>
                </div>
            </div>
            <h2 className="text-center small-header">Intimates</h2>
            <br />
            <div className="justify-content-center">
                <ProductCarousel {...products['intimates']} />
                <div className="text-center">
                    <Link className="btn btn-outline-dark see-more" to="/intimates">See More</Link>
                </div>
            </div>
        </div>
    </div>
);

export default Home;



